import { validSlugs } from "@/utils/slugs/projectSlugs";
import { notFound } from "next/navigation";

export default function ProjectDynamicPage({ params }) {
  const { projectId } = params;

  // slug array → provided by Next directly (safe in server component)
  const slugArray = params.slug || [];

  // First slug = module
  const currentModule =
    slugArray.length === 0
      ? Object.keys(validSlugs)[0] // overview is first
      : slugArray[0];

  // If slug not inside util → 404
  if (!Object.keys(validSlugs).includes(currentModule)) {
    return notFound();
  }

  // Dummy data
  const tasks = [
    { id: 1, title: "Task One", status: "Open" },
    { id: 2, title: "Task Two", status: "In Progress" },
  ];

  const bugs = [
    { id: 101, title: "Login Bug", severity: "High" },
    { id: 102, title: "UI Glitch", severity: "Low" },
  ];

  // ---------------- Module Views ----------------

  if (currentModule === "overview") {
    return (
      <div>
        <h1>Overview — {projectId}</h1>
        <p>This is dummy overview content.</p>
      </div>
    );
  }

  if (currentModule === "task") {
    return (
      <div>
        <h1>Task List</h1>
        <ul>
          {tasks.map((t) => (
            <li key={t.id}>
              {t.title} — {t.status}
            </li>
          ))}
        </ul>
      </div>
    );
  }

  if (currentModule === "bug") {
    return (
      <div>
        <h1>Bug List</h1>
        <ul>
          {bugs.map((b) => (
            <li key={b.id}>
              {b.title} — {b.severity}
            </li>
          ))}
        </ul>
      </div>
    );
  }

  // Add more modules (document, mom, dashboard) here later

  return notFound();
}
